export default {
	data() {
		return {
			alipy: true,
			wxpay: false,
			flag:false,
			baseUrl: ''
		};
	},
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	methods: {
		gerenClick() {
			console.log(this.wxpay);
			this.wxpay = true;
			this.alipy = false;
			// if(this.geren == false){
			//     this.qiye = false;
			// }
		},
		qiyeClick() {
			console.log(this.alipy);
			this.wxpay = false;
			this.alipy = true;
			// this.geren = false;
		},
		sure() {
			console.log(this.alipy);
			console.log(this.wxpay);
			this.flag = false;
			var alipy;
			if (this.alipy == true) {
				alipy = '2';
			} else {
				alipy = '1';
			}
			console.log(alipy);
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			// pay_type  1/微信 2/支付宝  3/小程序
			// type 1/VIP 2/超出后购买 3/自动续费 4/会员免费单词查询
			uni.request({
				url: this.baseUrl + '/api/vipOrder',
				method: 'POST',
				data: {
					pay_type: alipy,
					type: '2',
					vip_id: ''
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					if (res.data.code == 200) {
						console.log(res.data);
						// console.log(res.data.data.payData);
						// orderInfo 微信/支付宝订单数据
						// provider:provider  wxpay/微信 alipay支付宝
						let orderInfo = res.data.data.payData;
						console.log(orderInfo);
						uni.requestPayment({
							provider: 'alipay',
							orderInfo: orderInfo,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
							},
							fail: function(err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		xuFei() {
			this.flag = true;
		},
		cancel() {
			this.flag = false;
		}
	}
};
